import React, { useEffect } from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
// import HomeCarousel from "../components/HomeCarousel";
import VimeoEl from "../components/Vimeo"
import CreateTiles from "../components/createTiles"
// import NoWidows from "../utils/NoWidows";

// sass is currently global... can't see solution for this yet
import '../styles/global/index.scss'
import '../styles/nav.scss'
import '../styles/vimeo.scss'
import '../styles/home.scss'
import '../styles/work.scss'
import '../styles/about.scss'
import '../styles/contact.scss'
import '../styles/article.scss'

export default ({data}) => {
  const homeData = data.craft.entries[0];

  return (
    <Layout className="home">
      <SEO data={ homeData.seomatic } />

      {/* <HomeCarousel render={{ homeData:homeData.homeBanner }} /> */}

      <div className="row">
        <div className="col__2-5 col__m-1-5 col__s-0-5"/>
        <div className="col__7 col__m-8-5 col__s-11">
          <div className="home__heading">
            <h1 className="home__heading-text fontSize-h2"> { homeData.homeIntroText } </h1>
          </div>
        </div>
        <div className="col__2-5 col__m-1-5 col__s-0-5"/>
      </div>

      <div className="row article-media">
        <VimeoEl className="vimeo__video aspect__16-by-9" vimeoOptions={{ id: homeData.homeVimeo.split("/").pop(), loop: true }}/>
      </div>

      <div className="row">
        <div className="col__6 col__m-4 col__s-3"/>
        <div className="col__6 col__m-8 col__s-9">
          <div className="home__subheading">
            <h2 className="home__subheading-text fontSize-h3"> { homeData.homeSubText } </h2>
          </div>
        </div>
      </div>
      
      <div className="row">
        <CreateTiles render={{ data: homeData.projectsFeed }}/>
      </div>

      <div className="row">
      <div className="col__6 col__m-4 col__s-3"/>
      <div className="col__6 col__m-8 col__s-9">
          <div className="home__subheading">
            <h2 className="home__subheading-text fontSize-h3">We believe in total design. Our way of joining the dots and making design work harder. With a seamless connection of different design disciplines, we can bring identity to life in all ways. In motion, through spaces, in places, on the line, on the street, in the hand, in the&nbsp;moment.</h2>

            <Link className="home__subheading-link" to="/work">
              <p className="home__subheading-link-text fontSize-p2">See more of the work</p>
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export const query = graphql`
  query {
    craft {
      entries(section: home) {
        ... on Craft_Home {
          seomatic {
            metaTagContainer
            metaLinkContainer
            metaTitleContainer
            metaScriptContainer
            metaJsonLdContainer
          }
          homeBanner {
            ... on Craft_HomeBannerHomeBannerSlide {
              slideTagline
              slideClientName
              slideBannerImage {
                url
              }
              slideVideo {
                url
              }
              slideAmbientBackgroundVideo
              slideLinkToStory {
                slug
              }
            }
          }
          homeVimeo
          homeIntroText
          homeSubText
          projectsFeed(hidden: false) {
            ... on Craft_Projects {
              previewTile {
                url
                width
                height
                ... on Craft_UploadsVolume {
                  url

                  imageFile{
                    childImageSharp{
                      fluid(maxWidth: 650){
                        ...GatsbyImageSharpFluid_withWebp_noBase64
                      }
                    }
                  }

                  optimizedimages {
                    colorPalette
                  }
                }
              }
              title
              slug
              description {
                content
              }
            }
          }
        }
      }
    }
  }  
`;